import { css, modifier, styled } from '@lokalise/styled';
import type { FC } from 'react';

import { withLinkStyle } from '../../utils/styles';
import { value } from '../../utils/theme';
import type { PressableProps } from '../Pressable';
import { Pressable } from '../Pressable';

export type LinkProps = PressableProps;

// By adding explicit type, it provides better types in final package. Otherwise,
// for some reason styled causes the types to be messed up a bit.
export const Link: FC<LinkProps> = styled(Pressable)<LinkProps>`
	${withLinkStyle};

	${modifier(
		({ href }: PressableProps) => !href,
		css`
			&:disabled {
				cursor: not-allowed;
				color: ${value('color.text.disabled')};
			}
		`,
	)}
`;

Link.displayName = 'Link';
